import React from "react";
import { Link } from "react-router-dom";
import Button from "./Button";
import "./Header.css";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import DashboardIcon from "@material-ui/icons/Dashboard";

function Header({ button }) {
  return (
    <div className="header-container">
      <h1 className="main-head">User Management</h1>
      {button ? (
        <Link to="/add-user" className="link">
          <Button cls="add-user-btn" text="ADD" Icon={PersonAddIcon} />
        </Link>
      ) : (
        <Link to="/" className="link">
          <Button cls="dashboard-btn " text="Dashboard" Icon={DashboardIcon} />
        </Link>
      )}
    </div>
  );
}

export default Header;
