import React from "react";
import Button from "./Button";
import { Link } from "react-router-dom";
import AccountBoxIcon from "@material-ui/icons/AccountBox";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import "./Usercell.css";

function Usercell({ user, deleteHandler, viewHandler, onEdit }) {
  function editHandle(id) {
    onEdit(id);
  }
  return (
    <div className="user-cell-container">
      <div className="user-details-container">
        <div className="user-cell-id">
          <h4>{user.id}</h4>
        </div>
        <div className="user-cell-name">
          <h4>{user.name}</h4>
        </div>
      </div>

      <div className="user-cell-btn-container">
        <div
          onClick={() => {
            viewHandler(user.id);
          }}
        >
          <Link to="/user-details" className="link-user">
            <Button
              cls="view-user-btn user-cell-btn"
              text="View"
              Icon={AccountBoxIcon}
            />
          </Link>
        </div>
        <div onClick={() => editHandle(user.id)}>
          <Link to="/add-user" className="link-user">
            <Button
              cls="edit-user-btn user-cell-btn"
              text="Edit"
              Icon={EditIcon}
            />
          </Link>
        </div>

        <div
          onClick={() => {
            deleteHandler(user.id);
          }}
        >
          <Button
            cls="delete-user-btn user-cell-btn"
            text="Delete"
            Icon={DeleteIcon}
          />
        </div>
      </div>
    </div>
  );
}

export default Usercell;
