import React from "react";
import "./Edit.css";
import { useState } from "react";
import { useEffect } from "react";

function Editform({ Edit, editUser }) {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [id, setId] = useState("");
  async function userinput() {
    const user = await editUser;
    setName(user.name);
    setEmail(user.email);
    setPhone(user.phone);
    setId(user.id);
  }
  useEffect(() => userinput(), []);

  const onSubmit = (e) => {
    e.preventDefault();
    if (!name) {
      alert("Please add name");
      return;
    }
    Edit({ id, name, email, phone }, id);
    setName("");
    setEmail("");
    setPhone("");
  };

  return (
    <div className="edit-section-container">
      <form action="" className="user-form" onSubmit={onSubmit}>
        <label>Name: </label>
        <input
          type="text"
          placeholder="Enter name"
          value={name}
          onChange={(e) => {
            setName(e.target.value);
          }}
        />
        <label>Email: </label>
        <input
          type="text"
          placeholder="Enter Email"
          value={email}
          onChange={(e) => {
            setEmail(e.target.value);
          }}
        />
        <label>Phone: </label>
        <input
          type="number"
          placeholder="Enter Phone number"
          value={phone}
          onChange={(e) => {
            setPhone(e.target.valueAsNumber);
          }}
        />
        <input className="submit-btn" type="submit" value="Update User" />
      </form>
    </div>
  );
}

export default Editform;
