import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Header from "./Components/Header";
import Usertable from "./Components/Usertable";
import { useState } from "react";
import Addform from "./Components/Addform";
import { useEffect } from "react";
import axios from "axios";
import View from "./Components/View";
import Editform from "./Components/Editform";

function App() {
  // Variable containing user details
  const [users, setUsers] = useState([]);

  // Setting users variable at the start of the app
  useEffect(() => {
    const getusers = async () => {
      const usersfromserver = await fetchUsers();
      setUsers(usersfromserver);
    };
    getusers();
  }, []);

  // function of fetch user data
  const fetchUsers = async () => {
    const res = await axios.get(
      "https://obscure-thicket-43397.herokuapp.com/users/"
    );
    return res.data;
  };

  // Function to add user to the database

  async function addUser(newUser) {
    let id = 0;
    if (users.length === 0) {
      id = 1;
    } else {
      id = users[users.length - 1].id + 1;
    }

    const newUserData = { id, ...newUser };
    await axios.post(
      "https://obscure-thicket-43397.herokuapp.com/users/",
      newUserData
    );
    setUsers([...users, newUserData]);
  }

  // Function to handle deletion of user
  function deleteHandler(id) {
    axios
      .delete(`https://obscure-thicket-43397.herokuapp.com/users/${id}`)
      .then(() => {
        alert("deleted");
      });
    setUsers(users.filter((user) => user.id !== id));
  }

  // Function to handle view button and showing only selected user details
  let viewId = 0;
  const [viewUser, setViewUser] = useState({
    id: "No User Selected",
    name: "N/A",
    email: "N/A",
    phone: "N/A",
  });

  async function viewHandler(id) {
    //function to get user details from database based on id
    viewId = id;
    const viewUserValue = await reqUser(viewId);
    setViewUser(viewUserValue);
  }

  async function reqUser(id) {
    //requesting the user with the id recieved from the  button click
    const res = await axios.get(
      `https://obscure-thicket-43397.herokuapp.com/users/${id}`
    );
    return res.data;
  }
  let editFlag = true;
  let editUser = {};
  function onEdit(id) {
    editFlag = false;

    editUser = reqUser(id);
  }
  function Edit(userObject, id) {
    axios.put(
      `https://obscure-thicket-43397.herokuapp.com/users/${id}`,
      userObject
    );
  }
  return (
    <Router>
      <div className="App">
        <Route
          path="/"
          exact
          render={(props) => (
            <>
              <Header button={true} />
              {users.length !== 0 ? (
                <Usertable
                  userList={users}
                  onDelete={deleteHandler}
                  viewHandler={viewHandler}
                  onEdit={onEdit}
                />
              ) : (
                ""
              )}
            </>
          )}
        />
        <Route
          path="/add-user"
          render={() => (
            <>
              {editFlag ? (
                <>
                  <Header button={false} />
                  <Addform onAdd={addUser} />
                </>
              ) : (
                <>
                  <Header button={false} />
                  <Editform Edit={Edit} editUser={editUser} />
                  {(editFlag = true)}
                </>
              )}
            </>
          )}
        />
        <Route
          path="/user-details"
          render={() => (
            <>
              <View user={viewUser} />
            </>
          )}
        />
      </div>
    </Router>
  );
}

export default App;
